export default function AdminGuard(to, from, next) {
  const isAdmin = localStorage.getItem('isAdmin')
  const adminInfo = localStorage.getItem('adminInfo')
  
  if (to.path === '/admin/login') {
    // 
    if (isAdmin && adminInfo) {
      next('/admin/dashboard')
    } else {
      next()
    }
  } else {
    // 
    if (!isAdmin || !adminInfo) {
      next('/admin/login')
    } else {
      next()
    }
  }
} 