<template>
  <div class="auth-container">
    <el-card class="auth-card">
      <div class="auth-header">
        <h2></h2>
        <p></p>
      </div>

      <el-form ref="loginForm" :model="loginForm" :rules="loginRules" class="login-form">
        <el-form-item prop="username">
          <el-input v-model="loginForm.username" placeholder="">
            <template #prefix><el-icon class="icon"><User /></el-icon></template>
          </el-input>
        </el-form-item>

        <el-form-item prop="password">
          <el-input v-model="loginForm.password" type="password" placeholder="">
            <template #prefix><el-icon class="icon"><Lock /></el-icon></template>
          </el-input>
        </el-form-item>

        <div class="form-actions">
          <el-checkbox v-model="rememberMe"></el-checkbox>
          <router-link to="/forgot-password" class="forgot-password">?</router-link>
        </div>

        <el-form-item>
          <el-button type="primary" class="login-button" @click="handleLogin" :loading="loading">
            
          </el-button>
        </el-form-item>
      </el-form>

      <div class="register-link">
        <span>? </span>
        <router-link to="/register"></router-link>
      </div>
    </el-card>
  </div>
</template>

<!-- <script setup><script> -->
<script>
import { User, Lock } from '@element-plus/icons-vue';
import request from '../utils/request';
import { ElMessage } from 'element-plus';
import router from '../router'; // router

export default {
  name: 'Login',
  components: {
    User,
    Lock
  },
  data() {
    return {
      loginForm: {
        username: '',
        password: ''
      },
      loginRules: {
        username: [
          { required: true, message: '', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ]
      },
      rememberMe: false,
      loading: false
    };
  },
  methods: {
    // const
    handleLogin: async function() {
      this.loading = true;
      try {
        const response = await request.post('/auth/login', this.loginForm);
          console.log(':', response);
          
          // token
          const token = response.token || response.access_token || response.jwt || response.data?.token || response.data?.access_token;
          
          if (!token) {
            console.error('token');
            ElMessage.error(': ');
            this.loading = false;
            return;
          }
          
          localStorage.setItem('token', token);
          console.log('token:', token.substring(0, 10) + '...'); // token
        ElMessage.success('');
        router.push('/dashboard');
      } catch (error) {
        ElMessage.error(error.response?.data?.message || '');
      } finally {
        this.loading = false;
      }
    }
  }
};
</script>

<style scoped lang="scss">
.auth-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f5f7fa;
  padding: 20px;
}

.auth-card {
  width: 100%;
  max-width: 400px;
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  overflow: hidden;
}

.auth-header {
  text-align: center;
  padding: 2rem 0;
  background-color: #126bae;
  color: white;

  h2 {
    margin: 0 0 0.5rem;
    font-size: 1.8rem;
  }

  p {
    margin: 0;
    opacity: 0.9;
  }
}

.login-form {
  padding: 2rem;
}

.form-actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
}

.forgot-password {
  color: #126bae;
  text-decoration: none;
  font-size: 0.9rem;
}

.login-button {
  width: 100%;
  padding: 0.8rem;
  font-size: 1rem;
}

.register-link {
  text-align: center;
  padding: 1rem 2rem;
  border-top: 1px solid #eee;
  font-size: 0.9rem;

  a {
    color: #126bae;
    text-decoration: none;
    font-weight: 500;
  }
}
</style>