<template>
  <div class="article-detail-container">
    <el-card shadow="hover" class="detail-card">
      <div slot="header">
        <el-button @click="goBack" icon="ArrowLeft" class="back-button"></el-button>
      </div>

      <div class="article-header">
        <h1 class="article-title">{{ article.title }}</h1>
        <div class="article-meta">
          <span class="author"><User /> {{ article.authorName }}</span>
          <span class="publish-date">{{ formatDate(article.createdAt) }}</span>
          <span class="view-count"><Eye /> {{ article.viewCount }} </span>
        </div>
      </div>

      <div class="article-content" v-html="article.content"></div>

      <div class="article-actions">
        <el-button type="text" icon="Like" @click="handleLike">{{ article.likeCount }} </el-button>
        <el-button type="text" icon="Share" @click="handleShare"></el-button>
        <el-button type="text" icon="Edit" @click="handleEdit" v-if="isAuthor"></el-button>
      </div>

      <div class="comment-section">
        <h3> ({{ article.commentCount }})</h3>
        <!--  -->
      </div>
    </el-card>
  </div>
</template>

<script setup>
import { ref, onMounted, computed } from 'vue';
import { useRouter, useRoute } from 'vue-router';
import { ArrowLeft, User, Eye, Like, Share, Edit } from '@element-plus/icons-vue';
import request from '@/utils/request';
import { useStore } from 'vuex';
import { ElMessage } from 'element-plus'; // 

const router = useRouter();
const route = useRoute();
const store = useStore();
const article = ref({
  title: '...',
  content: '',
  authorName: '',
  createdAt: new Date(),
  viewCount: 0,
  likeCount: 0,
  commentCount: 0
});
const articleId = ref(route.params.id);

// 
const fetchArticleDetail = async () => {
  try {
    const response = await request.get(`/articles/${articleId.value}`);
    console.log(':', response);
    
    //  - responseresponse.data
    if (response) {
      //  {records: Array(1), total: 1, message: null}
      article.value = response.records?.[0] || response;
    } else {
      throw new Error('');
    }
  } catch (error) {
    console.error(':', error);
    ElMessage.error(`: ${error.message || ''}`);
    // 
    article.value = {
      title: '',
      content: '<div style="text-align:center;padding:40px;">ID</div>',
      authorName: '',
      createdAt: new Date(),
      viewCount: 0,
      likeCount: 0,
      commentCount: 0
    };
  }
};

// 
const formatDate = (dateString) => {
  return new Date(dateString).toLocaleString();
};

// 
const isAuthor = computed(() => {
  return store.state.user?.id === article.value.createdBy;
});

// 
const goBack = () => {
  router.push('/articles');
};

// 
const handleLike = async () => {
  try {
    await request.post(`/api/articles/${articleId.value}/like`);
    article.value.likeCount++;
    ElMessage.success('');
  } catch (error) {
    ElMessage.error('');
  }
};

// 
const handleShare = () => {
  // 
  ElMessage.info('');
};

// 
const handleEdit = () => {
  router.push(`/editor/${articleId.value}`);
};

onMounted(() => {
  fetchArticleDetail();
});
</script>

<style scoped>
.article-detail-container {
  padding: 20px;
  max-width: 1000px;
  margin: 0 auto;
}

.back-button {
  margin-bottom: 20px;
}

.article-header {
  margin-bottom: 30px;
  text-align: center;
}

.article-title {
  font-size: 28px;
  margin-bottom: 15px;
}

.article-meta {
  display: flex;
  justify-content: center;
  gap: 20px;
  color: #666;
  font-size: 14px;
}

.article-content {
  line-height: 1.8;
  font-size: 16px;
  margin-bottom: 40px;
}

.article-actions {
  border-top: 1px solid #eee;
  padding-top: 20px;
  display: flex;
  gap: 20px;
}

.comment-section {
  margin-top: 40px;
  border-top: 1px solid #eee;
  padding-top: 20px;
}
</style>