<template>
  <div class="admin-content">
    <!--  -->
    <div class="filters">
      <div class="search-box">
        <input 
          type="text" 
          v-model="searchKeyword" 
          placeholder="..."
          @keyup.enter="handleSearch"
        />
        <button @click="handleSearch"></button>
      </div>
      
      <div class="filter-options">
        <select v-model="statusFilter" @change="handleFilter">
          <option value=""></option>
          <option value="published"></option>
          <option value="pending"></option>
        </select>
      </div>
    </div>
    
    <!--  -->
    <div class="content-table">
      <table>
        <thead>
          <tr>
            <th>ID</th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="content in contentList" :key="content.id">
            <td>{{ content.id }}</td>
            <td class="title-cell">
              <div class="content-title" @click="viewContent(content)">
                {{ content.title }}
              </div>
            </td>
            <td>{{ content.createdBy }}</td>
            <td>
              <span class="status-badge" :class="content.isPublished ? 'published' : 'pending'">
                {{ content.isPublished ? '' : '' }}
              </span>
            </td>
            <td>{{ content.viewCount }}</td>
            <td>{{ formatDate(content.createdAt) }}</td>
            <td class="actions">
              <button 
                v-if="!content.isPublished" 
                class="btn-approve" 
                @click="approveContent(content.id)"
              >
                
              </button>
              <button 
                v-if="content.isPublished" 
                class="btn-reject" 
                @click="rejectContent(content.id)"
              >
                
              </button>
              <button class="btn-view" @click="viewContent(content)">
                
              </button>
              <button class="btn-delete" @click="deleteContent(content.id)">
                
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    
    <!--  -->
    <div class="pagination">
      <button 
        :disabled="currentPage === 0" 
        @click="changePage(currentPage - 1)"
      >
        
      </button>
      <span> {{ currentPage + 1 }}  {{ totalPages }} </span>
      <button 
        :disabled="currentPage >= totalPages - 1" 
        @click="changePage(currentPage + 1)"
      >
        
      </button>
    </div>
    
    <!--  -->
    <div v-if="showContentModal" class="modal-overlay" @click="closeModal">
      <div class="modal-content" @click.stop>
        <div class="modal-header">
          <h3>{{ selectedContent?.title }}</h3>
          <button class="close-btn" @click="closeModal"></button>
        </div>
        <div class="modal-body">
          <div class="content-info">
            <p><strong>:</strong> {{ selectedContent?.createdBy }}</p>
            <p><strong>:</strong> 
              <span class="status-badge" :class="selectedContent?.isPublished ? 'published' : 'pending'">
                {{ selectedContent?.isPublished ? '' : '' }}
              </span>
            </p>
            <p><strong>:</strong> {{ formatDate(selectedContent?.createdAt) }}</p>
            <p><strong>:</strong> {{ selectedContent?.viewCount }}</p>
          </div>
          <div class="content-preview">
            <h4>:</h4>
            <div class="content-text" v-html="selectedContent?.content"></div>
          </div>
        </div>
        <div class="modal-footer">
          <button 
            v-if="!selectedContent?.isPublished" 
            class="btn-approve" 
            @click="approveContent(selectedContent?.id)"
          >
            
          </button>
          <button 
            v-if="selectedContent?.isPublished" 
            class="btn-reject" 
            @click="rejectContent(selectedContent?.id)"
          >
            
          </button>
          <button class="btn-delete" @click="deleteContent(selectedContent?.id)">
            
          </button>
          <button class="btn-cancel" @click="closeModal"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import request from '../utils/request'

export default {
  name: 'AdminContent',
  setup() {
    const contentList = ref([])
    const currentPage = ref(0)
    const totalPages = ref(0)
    const searchKeyword = ref('')
    const statusFilter = ref('')
    const showContentModal = ref(false)
    const selectedContent = ref(null)
    const loading = ref(false)
    
    const fetchContentList = async () => {
      loading.value = true
      try {
        const params = {
          page: currentPage.value,
          size: 10
        }
        
        if (searchKeyword.value) {
          params.keyword = searchKeyword.value
        }
        
        if (statusFilter.value) {
          params.status = statusFilter.value
        }
        
        const response = await request.get('/admin/content/list', { params })
        contentList.value = response.data.content
        totalPages.value = response.data.totalPages
      } catch (error) {
        console.error(':', error)
      } finally {
        loading.value = false
      }
    }
    
    const handleSearch = () => {
      currentPage.value = 0
      fetchContentList()
    }
    
    const handleFilter = () => {
      currentPage.value = 0
      fetchContentList()
    }
    
    const changePage = (page) => {
      currentPage.value = page
      fetchContentList()
    }
    
    const approveContent = async (id) => {
      try {
        await request.put(`/admin/content/${id}/approve`)
        alert('')
        fetchContentList()
        closeModal()
      } catch (error) {
        alert(': ' + error.response?.data?.message)
      }
    }
    
    const rejectContent = async (id) => {
      try {
        await request.put(`/admin/content/${id}/reject`)
        alert('')
        fetchContentList()
        closeModal()
      } catch (error) {
        alert(': ' + error.response?.data?.message)
      }
    }
    
    const deleteContent = async (id) => {
      if (!confirm('')) return
      
      try {
        await request.delete(`/admin/content/${id}`)
        alert('')
        fetchContentList()
        closeModal()
      } catch (error) {
        alert(': ' + error.response?.data?.message)
      }
    }
    
    const viewContent = (content) => {
      selectedContent.value = content
      showContentModal.value = true
    }
    
    const closeModal = () => {
      showContentModal.value = false
      selectedContent.value = null
    }
    
    const formatDate = (dateString) => {
      if (!dateString) return ''
      return new Date(dateString).toLocaleString('zh-CN')
    }
    
    onMounted(() => {
      fetchContentList()
    })
    
    return {
      contentList,
      currentPage,
      totalPages,
      searchKeyword,
      statusFilter,
      showContentModal,
      selectedContent,
      loading,
      handleSearch,
      handleFilter,
      changePage,
      approveContent,
      rejectContent,
      deleteContent,
      viewContent,
      closeModal,
      formatDate
    }
  }
}
</script>

<style scoped>
.admin-content {
  max-width: 1200px;
  margin: 0 auto;
}

.filters {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;
  align-items: center;
}

.search-box {
  display: flex;
  gap: 10px;
  flex: 1;
}

.search-box input {
  flex: 1;
  padding: 10px 16px;
  border: 2px solid #e1e5e9;
  border-radius: 8px;
  font-size: 14px;
}

.search-box button {
  padding: 10px 20px;
  background: #667eea;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
}

.filter-options select {
  padding: 10px 16px;
  border: 2px solid #e1e5e9;
  border-radius: 8px;
  font-size: 14px;
  background: white;
}

.content-table {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  padding: 16px;
  text-align: left;
  border-bottom: 1px solid #e1e5e9;
}

th {
  background: #f8f9fa;
  font-weight: 600;
  color: #333;
}

.title-cell {
  max-width: 300px;
}

.content-title {
  cursor: pointer;
  color: #667eea;
  text-decoration: underline;
}

.content-title:hover {
  color: #5a6fd8;
}

.status-badge {
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 500;
}

.status-badge.published {
  background: #e8f5e8;
  color: #2e7d32;
}

.status-badge.pending {
  background: #fff3e0;
  color: #f57c00;
}

.actions {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.actions button {
  padding: 6px 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  font-weight: 500;
}

.btn-approve {
  background: #4caf50;
  color: white;
}

.btn-reject {
  background: #ff9800;
  color: white;
}

.btn-view {
  background: #2196f3;
  color: white;
}

.btn-delete {
  background: #f44336;
  color: white;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 20px;
}

.pagination button {
  padding: 8px 16px;
  border: 1px solid #e1e5e9;
  background: white;
  border-radius: 6px;
  cursor: pointer;
}

.pagination button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.pagination button:not(:disabled):hover {
  background: #f8f9fa;
}

/*  */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  border-radius: 12px;
  width: 90%;
  max-width: 800px;
  max-height: 80vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.modal-header {
  padding: 20px;
  border-bottom: 1px solid #e1e5e9;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-header h3 {
  margin: 0;
  color: #333;
}

.close-btn {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #666;
}

.modal-body {
  padding: 20px;
  overflow-y: auto;
  flex: 1;
}

.content-info {
  margin-bottom: 20px;
  padding: 16px;
  background: #f8f9fa;
  border-radius: 8px;
}

.content-info p {
  margin: 8px 0;
  color: #333;
}

.content-preview h4 {
  margin: 0 0 12px 0;
  color: #333;
}

.content-text {
  padding: 16px;
  border: 1px solid #e1e5e9;
  border-radius: 8px;
  background: #fafafa;
  max-height: 300px;
  overflow-y: auto;
  line-height: 1.6;
}

.modal-footer {
  padding: 20px;
  border-top: 1px solid #e1e5e9;
  display: flex;
  gap: 12px;
  justify-content: flex-end;
}

.modal-footer button {
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
}

.btn-cancel {
  background: #6c757d;
  color: white;
}

@media (max-width: 768px) {
  .filters {
    flex-direction: column;
    align-items: stretch;
  }
  
  .content-table {
    overflow-x: auto;
  }
  
  .actions {
    flex-direction: column;
  }
  
  .modal-content {
    width: 95%;
    margin: 20px;
  }
}
</style> 