<template>
  <div class="profile-container">
    <el-card class="profile-card" shadow="hover">
      <div slot="header" class="clearfix">
        <h2></h2>
      </div>

      <div class="profile-content">
        <!--  -->
        <div class="profile-overview">
          <div class="avatar-container">
            <el-avatar :size="100" class="user-avatar">
            <img v-if="imageUrl" :src="imageUrl" alt="User Avatar" />
            <span v-else>{{ userInitials }}</span>
          </el-avatar>
            <el-button type="primary" size="small" class="edit-avatar-btn" @click="openAvatarDialog"></el-button>

<el-dialog v-model="avatarDialogVisible" title="" width="400px" :append-to-body="true">
  <el-upload
    class="avatar-uploader"
    action="/api/avatar/upload"
    :show-file-list="false"
    :on-success="handleAvatarSuccess"
    :on-error="handleAvatarError"
    :before-upload="beforeAvatarUpload"
    :headers="authHeader"
  >
    <img v-if="imageUrl" :src="imageUrl" class="avatar" />
    <el-icon v-else class="avatar-uploader-icon"><Plus /></el-icon>
  </el-upload>
  <template #footer>
    <el-button @click="avatarDialogVisible = false"></el-button>
  </template>
</el-dialog>
          </div>

          <div class="user-info">
            <h3>{{ username }}</h3>
            <p class="user-email">{{ email }}</p>
            <p class="user-joined">: {{ formattedJoinDate }}</p>
          </div>
        </div>

        <el-divider></el-divider>

        <!--  -->
        <div class="account-settings">
          <h3></h3>
          <el-form ref="settingsForm" :model="settingsForm" label-width="120px" class="settings-form">
            <el-form-item label="">
              <el-input v-model="settingsForm.username" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="settingsForm.email" type="email" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="settingsForm.phone" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="settingsForm.oldPassword" type="password" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="settingsForm.newPassword" type="password" placeholder=""></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="saveSettings"></el-button>
              <el-button @click="resetForm"></el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted, computed } from 'vue';
import { Plus } from '@element-plus/icons-vue';
import { useRouter } from 'vue-router';
import request from '../utils/request';
import { ElMessage, ElLoading } from 'element-plus';

const authHeader = computed(() => {
  if (typeof window !== 'undefined' && window.localStorage) {
    const token = window.localStorage.getItem('token');
    return token ? { Authorization: `Bearer ${token}` } : {};
  }
  return {};
});

const router = useRouter();
const loading = ref(false);

// 
const username = ref('');
const email = ref('');
const joinDate = ref('');
const userInitials = computed(() => {
  return username.value ? username.value.charAt(0).toUpperCase() : 'U';
});
const formattedJoinDate = computed(() => {
  return joinDate.value ? new Date(joinDate.value).toLocaleDateString() : '';
});

// 
const settingsForm = reactive({
  username: '',
  email: '',
  phone: '',
  oldPassword: '',
  newPassword: ''
});

// 
const getUserProfile = async () => {
  const loadingInstance = ElLoading.service({ text: '...' });
  try {
    console.log('...');
    const response = await request.get('/api/auth/me');
    console.log(':', response);
    const userData = response.data || {};
console.log(':', userData);
console.log('URL:', userData.avatarUrl);
console.log(':', userData.joinDate, userData.createdAt);
username.value = userData.username || '';
email.value = userData.email || '';
joinDate.value = userData.joinDate || userData.createdAt || '';
console.log(':', joinDate.value);
// URL
if (userData.avatarUrl) {
  imageUrl.value = userData.avatarUrl;
  console.log('Loaded avatar URL:', imageUrl.value);
}

    // 
    settingsForm.username = username.value;
    settingsForm.email = email.value;
    // phoneemail
    // 
    // 
    // 
    // 
    console.log('Backend phone value:', userData.phone);
    const phoneValue = (userData.phone || '').trim();
    const emailValue = settingsForm.email.trim().toLowerCase();
    
    // 
    if (!phoneValue || phoneValue.includes('@') || phoneValue.toLowerCase() === emailValue) {
      settingsForm.phone = '';
    } else {
      settingsForm.phone = phoneValue;
    }
  } catch (error) {
    console.error(':', error);
    console.error(':', error.response?.status);
    console.error(':', error.response?.data);
    ElMessage.error(`: ${error.response?.data?.message || error.message}`);
    // 
    if (error.response?.status === 401 || error.response?.status === 403) {
      router.push('/login');
    }
  } finally {
    loadingInstance.close();
  }
};

// 
const saveSettings = async () => {
  if (!settingsForm.username.trim()) {
    ElMessage.warning('');
    return;
  }

  if (!settingsForm.email.trim()) {
    ElMessage.warning('');
    return;
  }

  loading.value = true;
  try {
    await request.put('/auth/profile', {
      username: settingsForm.username,
      email: settingsForm.email,
      phone: settingsForm.phone,
      oldPassword: settingsForm.oldPassword,
      newPassword: settingsForm.newPassword
    });

    ElMessage.success('');
    username.value = settingsForm.username;
    email.value = settingsForm.email;

    // 
    settingsForm.oldPassword = '';
    settingsForm.newPassword = '';
  } catch (error) {
    ElMessage.error(`: ${error.response?.data?.message || error.message}`);
    console.error('Error saving profile settings:', error);
  } finally {
    loading.value = false;
  }
};

// 
const resetForm = () => {
  settingsForm.username = username.value;
  settingsForm.email = email.value;
  settingsForm.oldPassword = '';
  settingsForm.newPassword = '';
};

// 
const handleAvatarSuccess = (response) => {
  if (response && response.avatarUrl) {
    imageUrl.value = response.avatarUrl;
    ElMessage.success('');
    avatarDialogVisible.value = false;
    // 
    getUserProfile();
  } else {
    ElMessage.error(': ');
  }
};

// 
const handleAvatarError = (error) => {
  ElMessage.error(`: ${error.message || ''}`);
};

// URL
const imageUrl = ref('');
const avatarDialogVisible = ref(false);

const openAvatarDialog = () => {
  console.log('Opening avatar dialog, current visibility:', avatarDialogVisible.value);
  avatarDialogVisible.value = true;
  console.log('After setting to true:', avatarDialogVisible.value);
};

// 
onMounted(() => {
  getUserProfile();
});
</script>

<style scoped>
.profile-container {
  max-width: 900px;
  margin: 20px auto;
  padding: 0 20px;
}

.profile-card {
  border-radius: 12px;
  overflow: hidden;
}

.profile-content {
  padding: 20px;
}

.profile-overview {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  padding-bottom: 20px;
  border-bottom: 1px solid #f0f0f0;
}

.avatar-container {
  margin-right: 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.user-avatar {
  background-color: #126bae;
  font-size: 40px;
  margin-bottom: 10px;
}

:deep .el-dialog {
  z-index: 3000 !important;
}

:deep .el-dialog__wrapper {
  z-index: 3000 !important;
}

.edit-avatar-btn {
  width: 100%;
}

.user-info h3 {
  margin: 0 0 10px 0;
  font-size: 24px;
  color: #333;
}

.user-email {
  color: #606266;
  margin: 0 0 5px 0;
}

.user-joined {
  color: #909399;
  margin: 0;
  font-size: 14px;
}

.account-settings h3 {
  margin: 0 0 20px 0;
  color: #333;
  font-size: 18px;
}

.settings-form {
  max-width: 600px;
}

.el-form-item {
  margin-bottom: 20px;
}
</style>