<template>
  <div class="auth-container">
    <el-card class="auth-card">
      <div class="auth-header">
        <h2></h2>
        <p></p>
      </div>

      <el-form ref="registerForm" :model="registerForm" :rules="registerRules" class="register-form">
        <el-form-item prop="username">
          <el-input v-model="registerForm.username" placeholder="">
            <template #prefix><el-icon class="icon"><User /></el-icon></template>
          </el-input>
        </el-form-item>

        <el-form-item prop="email">
          <el-input v-model="registerForm.email" type="email" placeholder="">
            <template #prefix><el-icon class="icon"><Message /></el-icon></template>
          </el-input>
        </el-form-item>

        <el-form-item prop="password">
          <el-input v-model="registerForm.password" type="password" placeholder="">
            <template #prefix><el-icon class="icon"><Lock /></el-icon></template>
          </el-input>
        </el-form-item>

        <el-form-item prop="confirmPassword">
          <el-input v-model="registerForm.confirmPassword" type="password" placeholder="">
            <template #prefix><el-icon class="icon"><Lock /></el-icon></template>
          </el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" class="register-button" @click="handleRegister" :loading="loading">
            
          </el-button>
        </el-form-item>
      </el-form>

      <div class="login-link">
        <span>? </span>
        <router-link to="/login"></router-link>
      </div>
    </el-card>
  </div>
</template>

<script>
import { User, Lock, Message } from '@element-plus/icons-vue';
import axios from 'axios';
import { ElMessage } from 'element-plus';
import { useRouter } from 'vue-router';

export default {
  name: 'Register',
  components: {
    User,
    Lock,
    Message
  },
  data() {
    return {
      registerForm: {
        username: '',
        email: '',
        password: '',
        confirmPassword: ''
      },
      registerRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'email', message: '', trigger: ['blur', 'change'] }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, message: '6', trigger: 'blur' }
        ],
        confirmPassword: [
          { required: true, message: '', trigger: 'blur' },
          { validator: this.validatePassword, trigger: 'blur' }
        ]
      },
      loading: false
    };
  },
  methods: {
    validatePassword(rule, value, callback) {
      if (value !== this.registerForm.password) {
        callback(new Error(''));
      } else {
        callback();
      }
    },

    async handleRegister() {
      this.loading = true;
      try {
        await this.$refs.registerForm.validate();
        const response = await axios.post('/api/auth/register', {
          username: this.registerForm.username,
          email: this.registerForm.email,
          password: this.registerForm.password
        });

        // tokentoken
        if (response.data.token) {
          localStorage.setItem('token', response.data.token);
          localStorage.setItem('username', response.data.username);
          this.$router.push('/dashboard');
          ElMessage.success('');
        } else {
          ElMessage.success(response.data.message || '');
          this.$router.push('/login');
        }
      } catch (error) {
        ElMessage.error(error.response?.data?.message || '');
      } finally {
        this.loading = false;
      }
    }
  }
};
</script>

<style scoped lang="scss">
/* Login.vue */
.auth-container {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #f5f7fa;
  padding: 20px;
}

.auth-card {
  width: 100%;
  max-width: 450px;
  border-radius: 10px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  overflow: hidden;
}

.auth-header {
  text-align: center;
  padding: 2rem 0;
  background-color: #126bae;
  color: white;

  h2 {
    margin: 0 0 0.5rem;
    font-size: 1.8rem;
  }

  p {
    margin: 0;
    opacity: 0.9;
  }
}

.register-form {
  padding: 2rem;
}

.register-button {
  width: 100%;
  padding: 0.8rem;
  font-size: 1rem;
}

.login-link {
  text-align: center;
  padding: 1rem 2rem;
  border-top: 1px solid #eee;
  font-size: 0.9rem;

  a {
    color: #126bae;
    text-decoration: none;
    font-weight: 500;
  }
}
</style>