<template>
  <div class="topic-selection-container">
    <el-card class="topic-card" shadow="hover">
      <div slot="header" class="clearfix">
        <h2></h2>
      </div>
      <el-form ref="form" :model="form" label-width="100px" class="topic-form">
        <el-form-item label="">
          <el-input v-model="form.keyword" placeholder="''''" @keyup.enter.native="generateTopics"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="form.difficulty" placeholder="" class="difficulty-select">
            <el-option label="" value="easy"></el-option>
            <el-option label="" value="medium"></el-option>
            <el-option label="" value="hard"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="generateTopics" :loading="loading"></el-button>
        </el-form-item>
      </el-form>
      <div v-if="topics.length > 0" class="topics-result">
        <h3></h3>
        <el-list>
          <el-list-item v-for="(topic, index) in topics" :key="index" class="topic-item" @click="goToEditor(topic)">
            <el-icon><fire-filled /></el-icon>
            {{ topic }}
          </el-list-item>
        </el-list>
      </div>
      <div v-else-if="showNoResult" class="no-result">
        <el-empty description=""></el-empty>
      </div>
    </el-card>
  </div>
</template>

<script setup>
import { ref, reactive } from 'vue';
import { useRouter } from 'vue-router';
import { FireFilled } from '@element-plus/icons-vue';
import { ElMessage } from 'element-plus';
import { generateTopics as generateTopicsApi } from '../api/topic';

const router = useRouter();
const form = reactive({
  keyword: '',
  difficulty: 'medium',
});
const topics = ref([]);
const loading = ref(false);
const showNoResult = ref(false);

function splitTopics(rawTopics) {
  if (Array.isArray(rawTopics)) {
    // 
    return rawTopics.flatMap(t => splitTopics(t));
  }
  if (typeof rawTopics === 'string') {
    // 
    return rawTopics
      .split(/[,;\n\r]+/)
      .map(t => t.trim())
      .filter(Boolean);
  }
  return [];
}

const generateTopics = async () => {
  if (!form.keyword.trim()) {
    ElMessage.warning('');
    return;
  }
  loading.value = true;
  try {
    const response = await generateTopicsApi({
      keyword: form.keyword,
      difficulty: form.difficulty,
      language: 'zh-CN',
    });
    if (response.success && response.topics && response.topics.length > 0) {
      // 
      topics.value = splitTopics(response.topics);
      showNoResult.value = false;
    } else {
      topics.value = [];
      showNoResult.value = true;
      ElMessage.error(response.message || '');
    }
  } catch (error) {
    topics.value = [];
    showNoResult.value = true;
    ElMessage.error(': ' + (error.message || ''));
  } finally {
    loading.value = false;
  }
};

const goToEditor = (topic) => {
  router.push({
    name: 'ContentEditor',
    params: {
      topicId: topic,  // params
    },
    query: {
      difficulty: form.difficulty,  // query
    },
  });
};
</script>

<style scoped>
.topic-selection-container {
  max-width: 800px;
  margin: 20px auto;
  padding: 0 20px;
}
.topic-card {
  border-radius: 12px;
  overflow: hidden;
}
/*  */
.clearfix {
  text-align: center;
  margin-bottom: 20px;
}
.clearfix h2 {
  margin: 0;
  display: inline-block;
}
.topic-form {
  margin-bottom: 30px;
}
.difficulty-select {
  width: 120px;
}
.topics-result {
  margin-top: 20px;
}
.topic-item {
  margin-bottom: 10px;
  cursor: pointer;
  transition: all 0.3s;
  display: flex;
  align-items: center;
  gap: 8px;
}
.topic-item:hover {
  background-color: #f5f7fa;
}
.no-result {
  margin-top: 40px;
  padding: 40px 0;
}
.el-collapse-item__content {
  padding-left: 20px;  /*  */
}

/*  */
.el-collapse-item__content > div {
  padding-left: 20px;
  margin-bottom: 10px;
}
</style>