<template>
  <div class="content-editor-container">
    <el-card shadow="hover">
      <div slot="header" class="clearfix">
        <el-button @click="goBack" icon="ArrowLeft" class="back-button"></el-button>
        <h2 class="editor-title"></h2>
      </div>

      <el-form ref="formRef" :model="form" class="content-form">
        <el-form-item label="">
          <el-input v-model="form.title" placeholder="" class="title-input"></el-input>
        </el-form-item>

        <el-form-item label="">
          <el-radio-group v-model="platformType" @change="convertContent">
            <el-radio-button label="default"></el-radio-button>
            <el-radio-button label="wechat"></el-radio-button>
            <el-radio-button label="douyin"></el-radio-button>
            <el-radio-button label="xiaohongshu"></el-radio-button>
          </el-radio-group>
        </el-form-item>

        <el-form-item label="">
          <!-- refv-model -->
          <QuillEditor
            ref="quillEditor"
            v-model:content="form.content"
            class="editor"
            :options="editorOptions"
            @update:content="handleContentChange"
          />
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="saveContent" :loading="saving"></el-button>
          <el-button @click="generateContent" :loading="generating">AI</el-button>
          <el-button @click="analyzeContent" class="ml-2"></el-button>
          <el-button @click="goToDashboard" class="ml-2" type="success"></el-button>
          <el-button @click="viewLastArticle" class="ml-2" type="info" :disabled="!lastSavedArticleId"></el-button>
          <el-button @click="testSave" class="ml-2" type="warning"></el-button>
          <el-button @click="quickLogin" class="ml-2" type="info"></el-button>
          <el-button @click="testSetContent" class="ml-2" type="secondary"></el-button>
          <el-button @click="debugContent" class="ml-2" type="danger"></el-button>
        </el-form-item>
      </el-form>
    </el-card>

    <!--  -->
    <el-dialog v-model="analysisDialogVisible" title="" width="600px">
      <div v-for="(suggestion, index) in analysisSuggestions" :key="index" class="suggestion-item">
        <el-alert
          :title="suggestion.title"
          :description="suggestion.content"
          :type="suggestion.type"
          show-icon
        ></el-alert>
      </div>
      <template #footer>
        <el-button @click="analysisDialogVisible = false"></el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script setup>
import { ref, reactive, onMounted, nextTick } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { ArrowLeft } from '@element-plus/icons-vue'
import axios from 'axios'
import { QuillEditor } from '@vueup/vue-quill'
import 'quill/dist/quill.snow.css'
import { ElMessage, ElDialog, ElAlert } from 'element-plus'
import { generateContent as generateContentApi, saveContent as saveContentApi } from '../api/topic'
import { marked } from 'marked';
// HTML
import DOMPurify from 'dompurify';
import request from '../utils/request';

// 
const route = useRoute()
const router = useRouter()

// 
const form = reactive({
  title: '',
  content: ''
})

// 
const platformType = ref('default')
const analysisDialogVisible = ref(false)
const analysisSuggestions = ref([])
const generating = ref(false)
const saving = ref(false)
const formRef = ref(null)
const quillEditor = ref(null)

// 
const isSettingContent = ref(false)

// ID
const lastSavedArticleId = ref(null)

// 
const editorOptions = {
  theme: 'snow',
  modules: {
    toolbar: [
      ['bold', 'italic', 'underline', 'strike'],
      [{ 'header': [1, 2, 3, false] }],
      [{ 'list': 'ordered'}, { 'list': 'bullet' }],
      [{ 'align': [] }],
      ['link', 'image'], // image
      ['clean']
    ],
    clipboard: {
      matchVisual: false
    }
  },
  // 
  sanitize: false,
  placeholder: '...'
};


// 
const goBack = () => router.push('/')
const goToDashboard = () => router.push('/dashboard')

// 
const getTopic = () => {
  const topic = route.params.topicId || route.query.keyword || '';
  // 
  console.log(':', topic);
  return topic || ''; // 
};
const getDifficulty = () => route.query.difficulty || 'medium'

// AI
const generateContent = async () => {
  const topic = getTopic();
  if (!topic) {
    ElMessage.warning('');
    return;
  }

  generating.value = true;
  try {
    form.title = topic;
    console.log(':', topic);
    
    const response = await generateContentApi({
      keyword: topic,
      difficulty: getDifficulty(),
      wordCount: 300
    });

    console.log('API:', response);

    if (!response?.content) {
      throw new Error('API');
    }

    console.log(':', response.content);

    // HTML
    let htmlContent;
    if (response.content.includes('<') && response.content.includes('>')) {
      // HTML
      htmlContent = response.content;
    } else {
      // HTML
      htmlContent = marked.parse(response.content);
    }
    
    console.log('HTML:', htmlContent);

    // HTML
    const sanitizedHtml = DOMPurify.sanitize(htmlContent);
    console.log('HTML:', sanitizedHtml);
    
    // HTML
    if (typeof sanitizedHtml !== 'string' || sanitizedHtml.trim().length < 10) {
      throw new Error('HTML');
    }

    // 
    isSettingContent.value = true;
    
    // form.content
    form.content = sanitizedHtml;
    console.log('form.content:', form.content);
    
    // 
    await nextTick();
    
    //  - setContents
    if (quillEditor.value && quillEditor.value.setContents) {
      try {
        // HTMLDelta
        const delta = quillEditor.value.clipboard.convert(sanitizedHtml);
        quillEditor.value.setContents(delta);
        console.log('setContents');
      } catch (error) {
        console.log('setContents:', error);
        // 
        if (quillEditor.value && quillEditor.value.setHTML) {
          try {
            quillEditor.value.setHTML(sanitizedHtml);
            console.log('setHTML');
          } catch (error2) {
            console.log('setHTML:', error2);
            // innerHTML
            if (quillEditor.value && quillEditor.value.root) {
              try {
                quillEditor.value.root.innerHTML = sanitizedHtml;
                console.log('innerHTML');
              } catch (error3) {
                console.log(':', error3);
              }
            }
          }
        }
      }
    } else if (quillEditor.value && quillEditor.value.setHTML) {
      try {
        quillEditor.value.setHTML(sanitizedHtml);
        console.log('setHTML');
      } catch (error) {
        console.log('setHTML:', error);
        // innerHTML
        if (quillEditor.value && quillEditor.value.root) {
          try {
            quillEditor.value.root.innerHTML = sanitizedHtml;
            console.log('innerHTML');
          } catch (error2) {
            console.log('innerHTML:', error2);
          }
        }
      }
    } else {
      console.log('');
    }
    
    // 
    setTimeout(() => {
      isSettingContent.value = false;
      console.log('');
    }, 100);
    
    // 
    setTimeout(() => {
      console.log(' - form.content:', typeof form.content);
      
      // HTML
      let actualContent = form.content;
      if (quillEditor.value && quillEditor.value.getHTML) {
        actualContent = quillEditor.value.getHTML();
      }
      
      console.log(' - :', actualContent.length);
      console.log(' - :', actualContent.substring(0, 100));
      
      if (actualContent.length < 20) { // 
        console.error('');
        ElMessage.warning('');
      } else {
        ElMessage.success('');
      }
    }, 200); // 
    
  } catch (error) {
    ElMessage.error(`: ${error.message || ''}`);
    console.error(':', error);
  } finally {
    generating.value = false;
  }
};

// 
const convertContent = () => {
  if (!form.content) return;

  let processedContent = form.content;
  switch(platformType.value) {
    case 'wechat':
      processedContent = form.content
        .replace(/<h2>(.*?)<\/h2>/g, '<h2 class="wechat-title">$1</h2>')
        .replace(/<p>/g, '<p class="wechat-paragraph">');
      break;
    case 'douyin':
      processedContent = form.content
        .replace(/<p>/g, '<p>[]')
        .replace(/<\/p>/g, '</p>\n[]')
        .replace(/<strong>(.*?)<\/strong>/g, '[]$1[/]');
      break;
    case 'xiaohongshu':
      const topicTags = form.title
        .split(/[ ]/)
        .filter(word => word.length > 2)
        .slice(0, 3)
        .map(word => `#${word}`)
        .join(' ');

      processedContent = form.content
        .replace(/<p>/g, '<p> ')
        .replace(/<\/p>/g, '</p>')
        + `<p>${topicTags}</p>`;
      break;
    default:
      // 
      break;
  }
  form.content = processedContent;
};

// 
const saveContent = async () => {
  // HTML
  let contentToSave = form.content;
  
  // form.contentHTML
  if (typeof form.content === 'object' && quillEditor.value && quillEditor.value.getHTML) {
    contentToSave = quillEditor.value.getHTML();
    console.log('HTML:', contentToSave);
  }
  
  // 
  if (!form.title || !contentToSave || typeof form.title !== 'string' || typeof contentToSave !== 'string') {
    ElMessage.warning('');
    console.error(':', { 
      titleType: typeof form.title, 
      contentType: typeof contentToSave,
      title: form.title,
      content: contentToSave
    });
    return;
  }

  // 
  if (form.title.trim() === '' || contentToSave.trim() === '') {
    ElMessage.warning('');
    return;
  }

  // 
  if (!checkLoginStatus()) return;

  saving.value = true;
  try {
    const topic = getTopic();
    const response = await saveContentApi({
      title: form.title,
      content: contentToSave,
      keyword: topic,
      difficulty: getDifficulty(),
      wordCount: 300
    });

    if (response.success && response.id) {
      lastSavedArticleId.value = response.id;
      ElMessage.success('');
      ElMessage.info('""');
    } else {
      ElMessage.error(response.warning || '');
    }
  } catch (error) {
    ElMessage.error(`: ${error.message || ''}`);
  } finally {
    saving.value = false;
  }
};

// 
const analyzeContent = async () => {
  // HTML
  let contentToAnalyze = form.content;
  
  // form.contentHTML
  if (typeof form.content === 'object' && quillEditor.value && quillEditor.value.getHTML) {
    contentToAnalyze = quillEditor.value.getHTML();
  }
  
  // 
  if (typeof form.title !== 'string' || typeof contentToAnalyze !== 'string' || !form.title.trim() || !contentToAnalyze.trim()) {
    ElMessage.warning('');
    console.error(':', { 
      titleType: typeof form.title, 
      contentType: typeof contentToAnalyze,
      title: form.title,
      content: contentToAnalyze
    });
    return;
  }

  try {
    ElMessage.loading('AI...');
    const response = await axios.post('/api/ai/analyze', {
      title: form.title,
      content: contentToAnalyze,
      platformType: platformType.value
    });

    ElMessage.closeAll();
    if (response.data.success) {
      analysisSuggestions.value = response.data.data.suggestions;
      analysisDialogVisible.value = true;
    } else {
      ElMessage.error(`: ${response.data.message}`);
    }
  } catch (error) {
    ElMessage.closeAll();
    ElMessage.error(`AI: ${error.message}`);
  }
};

// 
const testSave = async () => {
  try {
    const response = await request.post('/content/test-save');
    if (response.success) {
      ElMessage.success(`ID: ${response.id}`);
    } else {
      ElMessage.error(`: ${response.warning}`);
    }
  } catch (error) {
    ElMessage.error(`: ${error.message}`);
  }
};

// 
const quickLogin = async () => {
  try {
    const response = await request.post('/auth/login', {
      username: 'admin',
      password: 'admin123'
    });
    
    if (response.token) {
      localStorage.setItem('token', response.token);
      localStorage.setItem('tokenType', response.tokenType || 'Bearer');
      ElMessage.success('');
      console.log('token:', response.token);
    } else {
      ElMessage.error('');
    }
  } catch (error) {
    ElMessage.error(`: ${error.message}`);
  }
};

// 
const testSetContent = () => {
  const testContent = '<h2></h2><p></p><p></p>';
  
  console.log(':', testContent);
  form.content = testContent;
  
  // 
  nextTick(() => {
    // 1: setContents
    if (quillEditor.value && quillEditor.value.setContents) {
      try {
        const delta = quillEditor.value.clipboard.convert(testContent);
        quillEditor.value.setContents(delta);
        console.log('setContents');
        ElMessage.success('');
        return;
      } catch (error) {
        console.log('setContents:', error);
      }
    }
    
    // 2: setHTML
    if (quillEditor.value && quillEditor.value.setHTML) {
      try {
        quillEditor.value.setHTML(testContent);
        console.log('setHTML');
        ElMessage.success('');
        return;
      } catch (error) {
        console.log('setHTML:', error);
      }
    }
    
    // 3: innerHTML
    if (quillEditor.value && quillEditor.value.root) {
      try {
        quillEditor.value.root.innerHTML = testContent;
        console.log('innerHTML');
        ElMessage.success('');
        return;
      } catch (error) {
        console.log('innerHTML:', error);
      }
    }
    
    console.log('');
    ElMessage.warning('');
  });
};

// 
const checkLoginStatus = () => {
  const token = localStorage.getItem('token');
  if (!token) {
    ElMessage.warning('');
    router.push('/login');
    return false;
  }
  return true;
};

// 
const viewLastArticle = () => {
  if (lastSavedArticleId.value) {
    router.push(`/articles/${lastSavedArticleId.value}`);
  } else {
    ElMessage.warning('');
  }
};

// 
onMounted(() => {
  // 
  const token = localStorage.getItem('token');
  console.log(':', token ? '' : '');
  console.log('Token:', token);

  // 
  const topic = getTopic();
  if (topic) {
    form.title = topic;
    console.log(':', form.title);
  }
});
const handleContentChange = (content) => {
  // 
  if (isSettingContent.value) {
    console.log(' - ');
    return;
  }
  
  console.log(':', content);
  console.log(':', content.length);
  console.log(':', typeof content);
  
  // contentQuillDeltaHTML
  if (typeof content === 'object' && content.ops) {
    // Quill DeltaHTML
    if (quillEditor.value && quillEditor.value.getHTML) {
      const htmlContent = quillEditor.value.getHTML();
      console.log('HTML:', htmlContent);
      form.content = htmlContent;
    } else {
      // getHTML
      form.content = JSON.stringify(content);
    }
  } else if (typeof content === 'string') {
    form.content = content;
  } else {
    console.warn(':', content);
    form.content = String(content);
  }
};

// 
const debugContent = () => {
  console.log('===  ===');
  console.log('form.content:', form.content);
  console.log('form.content:', typeof form.content);
  console.log('form.title:', form.title);
  console.log('form.title:', typeof form.title);
  
  // QuillEditor
  console.log('quillEditor.value:', quillEditor.value);
  if (quillEditor.value) {
    console.log('quillEditor.value:', typeof quillEditor.value);
    console.log('quillEditor.value:', Object.getOwnPropertyNames(quillEditor.value));
    console.log('quillEditor.value.root:', quillEditor.value.root);
    
    // HTML
    if (quillEditor.value.getHTML) {
      const htmlContent = quillEditor.value.getHTML();
      console.log('HTML:', htmlContent);
      console.log('HTML:', htmlContent.length);
    }
    
    // 
    if (quillEditor.value.getText) {
      const textContent = quillEditor.value.getText();
      console.log(':', textContent);
      console.log(':', textContent.length);
    }
    
    // root
    if (quillEditor.value.root) {
      console.log('root.innerHTML:', quillEditor.value.root.innerHTML);
      console.log('root.textContent:', quillEditor.value.root.textContent);
    }
  } else {
    console.log('quillEditor.value');
  }
  
  console.log('===  ===');
};
</script>

<style scoped>
.content-editor-container { padding: 20px; }
.editor-title { text-align: center; margin: 0; }
.back-button { margin-right: 10px; }
.content-form { margin-top: 20px; }
.title-input { width: 100%; min-height: 40px; }
/*  */
.editor { height: 500px !important; border: 1px solid #e5e7eb; }
.suggestion-item { margin-bottom: 15px; }

/*  */
:deep(.ql-container) { height: calc(100% - 42px) !important; width: 100% !important; }
:deep(.ql-editor) { min-height: 400px; font-size: 16px; line-height: 1.6; }
:deep(.ql-toolbar) { width: 100% !important; }
.quill-editor { display: flex; flex-direction: column; width: 100% !important; }
</style>

