<template>
  <div class="admin-admins">
    <!--  -->
    <div class="actions-header">
      <button class="btn-add" @click="showAddModal = true">
        + 
      </button>
    </div>
    
    <!--  -->
    <div class="admins-table">
      <table>
        <thead>
          <tr>
            <th>ID</th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="admin in adminList" :key="admin.id">
            <td>{{ admin.id }}</td>
            <td>{{ admin.username }}</td>
            <td>{{ admin.realName || '-' }}</td>
            <td>{{ admin.email }}</td>
            <td>
              <span class="role-badge" :class="admin.role.toLowerCase()">
                {{ getRoleName(admin.role) }}
              </span>
            </td>
            <td>
              <span class="status-badge" :class="admin.isActive ? 'active' : 'inactive'">
                {{ admin.isActive ? '' : '' }}
              </span>
            </td>
            <td>{{ formatDate(admin.lastLogin) }}</td>
            <td>{{ formatDate(admin.createdAt) }}</td>
            <td class="actions">
              <button class="btn-edit" @click="editAdmin(admin)">
                
              </button>
              <button 
                v-if="admin.id !== currentAdminId"
                class="btn-delete" 
                @click="deleteAdmin(admin.id)"
              >
                
              </button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    
    <!--  -->
    <div class="pagination">
      <button 
        :disabled="currentPage === 0" 
        @click="changePage(currentPage - 1)"
      >
        
      </button>
      <span> {{ currentPage + 1 }}  {{ totalPages }} </span>
      <button 
        :disabled="currentPage >= totalPages - 1" 
        @click="changePage(currentPage + 1)"
      >
        
      </button>
    </div>
    
    <!-- / -->
    <div v-if="showAddModal || showEditModal" class="modal-overlay" @click="closeModal">
      <div class="modal-content" @click.stop>
        <div class="modal-header">
          <h3>{{ showEditModal ? '' : '' }}</h3>
          <button class="close-btn" @click="closeModal"></button>
        </div>
        <div class="modal-body">
          <div class="form-group">
            <label></label>
            <input 
              type="text" 
              v-model="adminForm.username" 
              :disabled="showEditModal"
              placeholder=""
            />
          </div>
          <div class="form-group">
            <label></label>
            <input 
              type="email" 
              v-model="adminForm.email" 
              placeholder=""
            />
          </div>
          <div class="form-group">
            <label></label>
            <input 
              type="text" 
              v-model="adminForm.realName" 
              placeholder=""
            />
          </div>
          <div class="form-group">
            <label></label>
            <input 
              type="text" 
              v-model="adminForm.phone" 
              placeholder=""
            />
          </div>
          <div class="form-group">
            <label></label>
            <input 
              type="password" 
              v-model="adminForm.password" 
              :placeholder="showEditModal ? '' : ''"
            />
          </div>
          <div class="form-group">
            <label></label>
            <select v-model="adminForm.role">
              <option value="MODERATOR"></option>
              <option value="ADMIN"></option>
              <option value="SUPER_ADMIN"></option>
            </select>
          </div>
          <div class="form-group">
            <label></label>
            <select v-model="adminForm.isActive">
              <option :value="true"></option>
              <option :value="false"></option>
            </select>
          </div>
        </div>
        <div class="modal-footer">
          <button class="btn-save" @click="saveAdmin">
            {{ showEditModal ? '' : '' }}
          </button>
          <button class="btn-cancel" @click="closeModal"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { ref, reactive, onMounted } from 'vue'
import request from '../utils/request'

export default {
  name: 'AdminAdmins',
  setup() {
    const adminList = ref([])
    const currentPage = ref(0)
    const totalPages = ref(0)
    const showAddModal = ref(false)
    const showEditModal = ref(false)
    const selectedAdmin = ref(null)
    const currentAdminId = ref(null)
    const loading = ref(false)
    
    const adminForm = reactive({
      username: '',
      email: '',
      realName: '',
      phone: '',
      password: '',
      role: 'ADMIN',
      isActive: true
    })
    
    const fetchAdminList = async () => {
      loading.value = true
      try {
        const params = {
          page: currentPage.value,
          size: 10
        }
        
        const response = await request.get('/admin/admins', { params })
        adminList.value = response.data.content
        totalPages.value = response.data.totalPages
      } catch (error) {
        console.error(':', error)
      } finally {
        loading.value = false
      }
    }
    
    const changePage = (page) => {
      currentPage.value = page
      fetchAdminList()
    }
    
    const editAdmin = (admin) => {
      selectedAdmin.value = admin
      adminForm.username = admin.username
      adminForm.email = admin.email
      adminForm.realName = admin.realName || ''
      adminForm.phone = admin.phone || ''
      adminForm.password = ''
      adminForm.role = admin.role
      adminForm.isActive = admin.isActive
      showEditModal.value = true
    }
    
    const saveAdmin = async () => {
      try {
        if (showEditModal.value) {
          // 
          const updateData = { ...adminForm }
          if (!updateData.password) {
            delete updateData.password
          }
          await request.put(`/admin/admins/${selectedAdmin.value.id}`, updateData)
          alert('')
        } else {
          // 
          await request.post('/admin/admins', adminForm)
          alert('')
        }
        closeModal()
        fetchAdminList()
      } catch (error) {
        alert(': ' + error.response?.data?.message)
      }
    }
    
    const deleteAdmin = async (id) => {
      if (!confirm('')) return
      
      try {
        await request.delete(`/admin/admins/${id}`)
        alert('')
        fetchAdminList()
      } catch (error) {
        alert(': ' + error.response?.data?.message)
      }
    }
    
    const closeModal = () => {
      showAddModal.value = false
      showEditModal.value = false
      selectedAdmin.value = null
      Object.assign(adminForm, {
        username: '',
        email: '',
        realName: '',
        phone: '',
        password: '',
        role: 'ADMIN',
        isActive: true
      })
    }
    
    const getRoleName = (role) => {
      const roleNames = {
        'SUPER_ADMIN': '',
        'ADMIN': '',
        'MODERATOR': ''
      }
      return roleNames[role] || role
    }
    
    const formatDate = (dateString) => {
      if (!dateString) return '-'
      return new Date(dateString).toLocaleString('zh-CN')
    }
    
    onMounted(() => {
      // ID
      const adminInfo = localStorage.getItem('adminInfo')
      if (adminInfo) {
        const admin = JSON.parse(adminInfo)
        currentAdminId.value = admin.id
      }
      fetchAdminList()
    })
    
    return {
      adminList,
      currentPage,
      totalPages,
      showAddModal,
      showEditModal,
      selectedAdmin,
      currentAdminId,
      adminForm,
      loading,
      changePage,
      editAdmin,
      saveAdmin,
      deleteAdmin,
      closeModal,
      getRoleName,
      formatDate
    }
  }
}
</script>

<style scoped>
.admin-admins {
  max-width: 1200px;
  margin: 0 auto;
}

.actions-header {
  margin-bottom: 20px;
}

.btn-add {
  padding: 12px 24px;
  background: #4caf50;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
}

.admins-table {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  padding: 16px;
  text-align: left;
  border-bottom: 1px solid #e1e5e9;
}

th {
  background: #f8f9fa;
  font-weight: 600;
  color: #333;
}

.role-badge {
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 500;
}

.role-badge.super_admin {
  background: #ffebee;
  color: #c62828;
}

.role-badge.admin {
  background: #e3f2fd;
  color: #1565c0;
}

.role-badge.moderator {
  background: #f3e5f5;
  color: #7b1fa2;
}

.status-badge {
  padding: 4px 8px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 500;
}

.status-badge.active {
  background: #e8f5e8;
  color: #2e7d32;
}

.status-badge.inactive {
  background: #ffebee;
  color: #c62828;
}

.actions {
  display: flex;
  gap: 8px;
  flex-wrap: wrap;
}

.actions button {
  padding: 6px 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 12px;
  font-weight: 500;
}

.btn-edit {
  background: #2196f3;
  color: white;
}

.btn-delete {
  background: #f44336;
  color: white;
}

.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin-top: 20px;
}

.pagination button {
  padding: 8px 16px;
  border: 1px solid #e1e5e9;
  background: white;
  border-radius: 6px;
  cursor: pointer;
}

.pagination button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

.pagination button:not(:disabled):hover {
  background: #f8f9fa;
}

/*  */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}

.modal-content {
  background: white;
  border-radius: 12px;
  width: 90%;
  max-width: 600px;
  max-height: 80vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.modal-header {
  padding: 20px;
  border-bottom: 1px solid #e1e5e9;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.modal-header h3 {
  margin: 0;
  color: #333;
}

.close-btn {
  background: none;
  border: none;
  font-size: 24px;
  cursor: pointer;
  color: #666;
}

.modal-body {
  padding: 20px;
  overflow-y: auto;
  flex: 1;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  margin-bottom: 8px;
  color: #333;
  font-weight: 500;
  font-size: 14px;
}

.form-group input,
.form-group select {
  width: 100%;
  padding: 12px 16px;
  border: 2px solid #e1e5e9;
  border-radius: 8px;
  font-size: 14px;
  box-sizing: border-box;
}

.form-group input:focus,
.form-group select:focus {
  outline: none;
  border-color: #667eea;
}

.form-group input:disabled {
  background: #f8f9fa;
  color: #666;
}

.modal-footer {
  padding: 20px;
  border-top: 1px solid #e1e5e9;
  display: flex;
  gap: 12px;
  justify-content: flex-end;
}

.modal-footer button {
  padding: 10px 20px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
}

.btn-save {
  background: #4caf50;
  color: white;
}

.btn-cancel {
  background: #6c757d;
  color: white;
}

@media (max-width: 768px) {
  .admins-table {
    overflow-x: auto;
  }
  
  .actions {
    flex-direction: column;
  }
  
  .modal-content {
    width: 95%;
    margin: 20px;
  }
}
</style> 