<template>
  <div id="app">
    <el-container class="app-container">
      <!--  -->
      <el-header class="app-header">
        <div class="logo-container">
          <el-icon class="logo-icon"><PenTool /></el-icon>
          <span class="logo-text">MediaGenius</span>
          <span class="platform-text"></span>
        </div>
        <el-menu :default-active="activeIndex" mode="horizontal" class="header-menu">
          <el-menu-item index="1" @click="goToHome"></el-menu-item>
          <el-menu-item index="2" @click="goToFeatures"></el-menu-item>
          <el-menu-item index="3" @click="goToTutorial"></el-menu-item>
          <el-menu-item index="4" @click="goToArticles"></el-menu-item>
          <el-menu-item index="5" @click="goToTopicSelection"></el-menu-item>
          <el-menu-item index="6" @click="goToDashboard"></el-menu-item>
          <el-menu-item v-if="isLoggedIn" index="7" @click="goToProfile"></el-menu-item>
        </el-menu>
        <div class="auth-buttons">
          <!--  -->
          <template v-if="isLoggedIn">
            <el-dropdown class="user-dropdown">
              <span class="user-name">{{ username }}</span>
              <template #dropdown>
                <el-dropdown-menu>
                  <el-dropdown-item @click="goToProfile"></el-dropdown-item>
                  <el-dropdown-item @click="logout"></el-dropdown-item>
                </el-dropdown-menu>
              </template>
            </el-dropdown>
          </template>
          <template v-else>
            <el-button type="text" class="login-btn" @click="goToLogin"></el-button>
            <el-button type="primary" class="register-btn" @click="goToRegister"></el-button>
          </template>
        </div>
      </el-header>

      <!--  -->
      <el-main class="app-main">
        <router-view />
      </el-main>

      <!--  -->
      <el-footer class="app-footer">
        <div class="footer-container">
          <div class="footer-logo">MediaGenius</div>
          
          <div class="footer-links">
            <!--  -->
            <div class="link-group">
              <h3 class="link-title"></h3>
              <a class="footer-link"></a>
              <a class="footer-link"></a>
              <a class="footer-link"></a>
            </div>
          
            <!--  -->
            <div class="link-group">
              <h3 class="link-title"></h3>
              <a class="footer-link"></a>
              <a class="footer-link"></a>
              <a class="footer-link"></a>
            </div>
          
            <!--  -->
            <div class="link-group">
              <h3 class="link-title"></h3>
              <a class="footer-link"></a>
              <a class="footer-link"></a>
              <a class="footer-link"></a>
            </div>
          </div>
          
          <div class="copyright">
             2025 MediaGenius. 
          </div>
        </div>
      </el-footer>
    </el-container>
  </div>
</template>

<script setup>
import { ref, onMounted, watch } from 'vue';
import { useRouter, useRoute } from 'vue-router';
import { PenTool } from '@element-plus/icons-vue';
import request from './utils/request';
import { ElMessage } from 'element-plus';

// 
const router = useRouter();
const route = useRoute();

// 
const activeIndex = ref('1');
const isLoggedIn = ref(false);
const username = ref('');

// 
const goToHome = () => router.push('/');
const goToFeatures = () => router.push('/features');
const goToTutorial = () => router.push('/tutorial');
const goToAbout = () => router.push('/about');
const goToTopicSelection = () => router.push('/topics');
const goToDashboard = () => router.push('/dashboard');
const goToLogin = () => router.push('/login');
const goToRegister = () => router.push('/register');
const goToProfile = () => router.push('/profile');
const goToArticles = () => router.push('/articles');

// 
const updateActiveMenu = (path) => {
  switch(path) {
    case '/': activeIndex.value = '1'; break;
    case '/features': activeIndex.value = '2'; break;
    case '/tutorial': activeIndex.value = '3'; break;
    case '/about': activeIndex.value = '4'; break;
    case '/topics': activeIndex.value = '5'; break;
    case '/dashboard': activeIndex.value = '6'; break;
    case '/profile': activeIndex.value = '7'; break;
    default: activeIndex.value = '1';
  }
};

// 
const getUserInfo = async () => {
  try {
    // URL/api
    const response = await request.get('/auth/me');
    username.value = response.username || '';
    isLoggedIn.value = true;
  } catch (error) {
    // token
    localStorage.removeItem('token');
    localStorage.removeItem('tokenType');
    isLoggedIn.value = false;
    username.value = '';
  }
};

// 
const logout = async () => {
  try {
    await request.post('/api/auth/logout');
  } catch (error) {
    console.error('Logout error:', error);
  } finally {
    localStorage.removeItem('token');
    localStorage.removeItem('tokenType');
    isLoggedIn.value = false;
    username.value = '';
    router.push('/login');
    ElMessage.success('');
  }
};

// 
onMounted(() => {
  updateActiveMenu(route.path);
  router.beforeEach((to) => updateActiveMenu(to.path));
  
  // 
  const token = localStorage.getItem('token');
  if (token) {
    getUserInfo();
  }
});

// 
watch(
  () => route.path,
  () => {
    const token = localStorage.getItem('token');
    if (token && !isLoggedIn.value) {
      getUserInfo();
    } else if (!token && isLoggedIn.value) {
      isLoggedIn.value = false;
    }
  }
);
</script>

<style scoped>
