<template>
  <div class="article-list-container">
    <el-card shadow="hover">
      <!--  -->
      <!-- <div v-if="debugInfo" class="debug-info"> -->
        <!-- <p>: {{ debugInfo }}</p> -->
        <!-- <p>: {{ articles[0]?.title || '' }}</p>  -->
      <!-- </div> -->
      
      <!--  -->
      <div slot="header" class="clearfix">
        <h2 class="page-title"></h2>
        <el-button type="primary" @click="goToEditor">
          <Plus /> 
        </el-button>
      </div>

      <!--  -->
      <div v-if="articles.length === 0 && !loading" class="empty-state">
        <p></p>
      </div>

      <div v-else-if="loading" class="loading-state">
        <p>...</p>
      </div>

      <!--  -->
      <!-- el-listel-list-item -->
      <div class="article-list">
        <div
          v-for="article in articles"
          :key="article.id"
          class="article-item"
          @click="goToDetail(article.id)"
        >
          <div class="article-card">
            <div class="article-header">
              <h3 class="article-title">{{ article.title }}</h3>
              <span class="publish-date">{{ formatDate(article.createdAt) }}</span>
            </div>
            <div class="article-meta">
              <span class="meta-item"> {{ article.viewCount }} </span>
              <span class="meta-item"> {{ article.likeCount }} </span>
              <span class="meta-item"> {{ article.commentCount }} </span>
            </div>
          </div>
        </div>
      </div>

      <!--  -->
      <el-pagination
        v-if="total > 0"
        :current-page="currentPage"
        :page-size="pageSize"
        :total="total"
        class="pagination"
        @current-change="handlePageChange"
      ></el-pagination>
    </el-card>
  </div>
</template>

<script setup>
import { ref, onMounted } from 'vue';
import request from '@/utils/request';
import { ElMessage } from 'element-plus';
import { useRouter } from 'vue-router';
import { Plus, Search, Eye, Like, Comment } from '@element-plus/icons-vue';
// 
import { ElCard, ElInput, ElList, ElListItem, ElPagination } from 'element-plus';

// 
const components = {
  Plus,
  Search,
  Eye,
  Like,
  Comment,
  ElCard,
  ElInput,
  ElList,
  ElListItem,
  ElPagination
};
const router = useRouter();
const articles = ref([]);
const total = ref(0);
const currentPage = ref(1);
const pageSize = ref(10);
const searchKeyword = ref('');
const loading = ref(false);
const debugInfo = ref('');

const fetchArticles = async () => {
  debugInfo.value = 'fetchArticles';
  console.log('fetchArticles called');
  loading.value = true;
  try {
    // 
    const token = localStorage.getItem('token');
    if (!token) {
      debugInfo.value = '';
      ElMessage.warning('');
      router.push('/login');
      return;
    }

    debugInfo.value = 'API';
    console.log('Sending request to /api/articles');
    const response = await request.get('/articles', { // /api
      params: {
        page: currentPage.value,
        size: pageSize.value,
        keyword: searchKeyword.value
      }
    });
    debugInfo.value = 'API';
    console.log('Response received:', response);
    articles.value = response.records || [];
    total.value = response.total || 0;
    debugInfo.value = `${articles.value.length}`;
  } catch (error) {
    debugInfo.value = 'API: ' + (error.message || '');
    console.error('Error details:', error);
    articles.value = [];
    total.value = 0;
  } finally {
    loading.value = false;
  }
};

// 
onMounted(() => {
  console.log('ArticleList component mounted');
  debugInfo.value = '';
  fetchArticles();
});

// 
const formatDate = (dateString) => {
  if (!dateString) return '';
  return new Date(dateString).toLocaleString();
};

// 
const goToDetail = (articleId) => {
  router.push(`/articles/${articleId}`);
};

// 
const goToEditor = () => {
  router.push('/editor');
};

// 
const handlePageChange = (page) => {
  currentPage.value = page;
  fetchArticles();
};
</script>

<style scoped>
.article-list-container {
  padding: 20px;
}

.page-title {
  display: inline-block;
  margin-right: 20px;
}

.search-input {
  margin-bottom: 20px;
  max-width: 500px;
}

.article-item {
  cursor: pointer;
  padding: 10px 0;
}

.article-card {
  width: 100%;
  transition: transform 0.2s;
}

.article-card:hover {
  transform: translateY(-5px);
}

.article-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.article-title {
  margin: 0;
  font-size: 18px;
  color: #333; /*  */
  padding: 5px 0; /*  */
}

.publish-date {
  color: #666;
  font-size: 14px;
}

.article-meta {
  display: flex;
  gap: 20px;
  color: #666;
  font-size: 14px;
}

.meta-item {
  display: flex;
  align-items: center;
  gap: 5px;
}

.pagination {
  margin-top: 20px;
  text-align: right;
}

/*  */
.debug-info {
  background-color: #fff3cd;
  padding: 10px;
  margin-bottom: 15px;
  border-radius: 4px;
}

.empty-state,
.loading-state {
  text-align: center;
  padding: 40px 0;
  color: #666;
}
</style>