<template>
  <div class="dashboard-container">
    <!--  -->
    <div class="dashboard-header">
      <div class="header-left">
        <el-button @click="goBack" icon="ArrowLeft" class="back-button" type="primary" plain>
          
        </el-button>
        <h1 class="dashboard-title"> - </h1>
      </div>
      <div class="header-right">
        <el-select v-model="timeRange" @change="handleTimeRangeChange" placeholder="" size="small">
          <el-option label="7" value="7"></el-option>
          <el-option label="30" value="30"></el-option>
          <el-option label="90" value="90"></el-option>
        </el-select>
        <el-button @click="refreshData" icon="Refresh" size="small" :loading="loading">
          
        </el-button>
      </div>
    </div>

    <!--  -->
    <div class="stats-grid">
      <div v-if="statistics.length === 0" class="no-data">
        <p>...</p>
      </div>
      <div class="stat-card" v-for="(stat, index) in statistics" :key="index">
        <div class="stat-icon" :style="{ backgroundColor: stat.color }">
          <i :class="stat.icon"></i>
        </div>
        <div class="stat-content">
          <div class="stat-value">{{ formatNumber(stat.value) }}</div>
          <div class="stat-label">{{ stat.label }}</div>
          <div class="stat-trend" :class="(stat.trend || 0) > 0 ? 'positive' : 'negative'">
            <i :class="(stat.trend || 0) > 0 ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"></i>
            {{ Math.abs(stat.trend || 0) }}%
          </div>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="charts-section">
      <!--  -->
      <div class="chart-container">
        <div class="chart-header">
          <h3></h3>
          <div class="chart-actions">
            <el-radio-group v-model="trafficMetric" size="small" @change="updateTrafficChart">
              <el-radio-button label="reads"></el-radio-button>
              <el-radio-button label="likes"></el-radio-button>
              <el-radio-button label="shares"></el-radio-button>
              <el-radio-button label="comments"></el-radio-button>
            </el-radio-group>
          </div>
        </div>
        <div class="chart-content">
          <canvas ref="trafficChart" height="300"></canvas>
        </div>
      </div>

      <!--  -->
      <div class="chart-container">
        <div class="chart-header">
          <h3></h3>
        </div>
        <div class="chart-content">
          <canvas ref="engagementChart" height="300"></canvas>
        </div>
      </div>
    </div>

    <!--  -->
    <div class="analysis-section">
      <div class="chart-container">
        <div class="chart-header">
          <h3></h3>
        </div>
        <div class="chart-content">
          <canvas ref="contentChart" height="300"></canvas>
        </div>
      </div>

      <!--  -->
      <div class="table-container">
        <div class="chart-header">
          <h3></h3>
        </div>
        <el-table :data="hotContent" stripe style="width: 100%">
          <el-table-column prop="rank" label="" width="80" align="center">
            <template #default="scope">
              <div class="rank-badge" :class="`rank-${scope.row.rank}`">
                {{ scope.row.rank }}
              </div>
            </template>
          </el-table-column>
          <el-table-column prop="title" label="" min-width="200"></el-table-column>
          <el-table-column prop="category" label="" width="100"></el-table-column>
          <el-table-column prop="reads" label="" width="100" align="center"></el-table-column>
          <el-table-column prop="likes" label="" width="100" align="center"></el-table-column>
          <el-table-column prop="shares" label="" width="100" align="center"></el-table-column>
          <el-table-column prop="engagement" label="" width="100" align="center">
            <template #default="scope">
              <span class="engagement-rate">{{ scope.row.engagement }}%</span>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>

    <!--  -->
    <div v-if="loading" class="loading-overlay">
      <el-loading-spinner></el-loading-spinner>
      <p>...</p>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, nextTick } from 'vue'
import { useRouter } from 'vue-router'
import { ElMessage } from 'element-plus'
import { getDashboardStats, getTrafficData, getContentAnalysis, getUserEngagement } from '@/api/topic'
import Chart from 'chart.js/auto'

// Chart.js
console.log('Chart.js:', Chart.version)

const router = useRouter()
const loading = ref(false)
const timeRange = ref('7')
const trafficMetric = ref('reads')

// 
const trafficChart = ref(null)
const engagementChart = ref(null)
const contentChart = ref(null)

// 
const dashboardData = ref({})
const statistics = ref([])
const hotContent = ref([])

// 
let trafficChartInstance = null
let engagementChartInstance = null
let contentChartInstance = null

// 
const goBack = () => {
  router.go(-1)
}

// 
const formatNumber = (num) => {
  if (num === undefined || num === null || isNaN(num)) {
    return '0'
  }
  if (num >= 10000) {
    return (num / 10000).toFixed(1) + ''
  }
  return num.toLocaleString()
}

// 
const handleTimeRangeChange = async () => {
  await loadTrafficData()
}

// 
const refreshData = async () => {
  await loadDashboardData()
}

// Dashboard
const loadDashboardData = async () => {
  loading.value = true
  try {
    const response = await getDashboardStats()
    console.log('API:', response)
    
    // JSON
    if (response && typeof response === 'object' && !response.includes('<!doctype html>')) {
      dashboardData.value = response
      
      // 
      if (!dashboardData.value.statistics) {
        dashboardData.value.statistics = {}
      }
      if (!dashboardData.value.traffic) {
        dashboardData.value.traffic = {
          dates: [],
          reads: [],
          likes: [],
          shares: [],
          comments: []
        }
      }
      if (!dashboardData.value.userEngagement) {
        dashboardData.value.userEngagement = {
          labels: [],
          data: [],
          colors: []
        }
      }
      
      // 
      updateStatistics()
      
      // 
      await nextTick()
      updateCharts()
    } else {
      console.warn('APIHTML')
      // 
    }
  } catch (error) {
    console.error('Dashboard:', error)
    ElMessage.error(': ' + (error.message || ''))
    // 
  } finally {
    loading.value = false
  }
}

// 
const loadTrafficData = async () => {
  try {
    const response = await getTrafficData(parseInt(timeRange.value))
    dashboardData.value.traffic = response
    updateTrafficChart()
  } catch (error) {
    ElMessage.error(': ' + error.message)
  }
}

// 
const updateStatistics = () => {
  const data = dashboardData.value.statistics || {}
  console.log(':', data)
  
  statistics.value = [
    {
      label: '',
      value: data.totalReads || 0,
      trend: data.readsTrend || 0,
      icon: 'el-icon-view',
      color: '#409EFF'
    },
    {
      label: '',
      value: data.avgLikesRate || 0,
      trend: data.likesTrend || 0,
      icon: 'el-icon-star-on',
      color: '#67C23A'
    },
    {
      label: '',
      value: data.shares || 0,
      trend: data.sharesTrend || 0,
      icon: 'el-icon-share',
      color: '#E6A23C'
    },
    {
      label: '',
      value: data.comments || 0,
      trend: data.commentsTrend || 0,
      icon: 'el-icon-chat-dot-round',
      color: '#F56C6C'
    },
    {
      label: '',
      value: data.totalContent || 0,
      trend: data.contentGrowth || 0,
      icon: 'el-icon-document',
      color: '#909399'
    }
  ]
  
  console.log(':', statistics.value)
}

// 
const updateTrafficChart = () => {
  console.log('...')
  console.log('trafficChart ref:', trafficChart.value)
  console.log('traffic data:', dashboardData.value.traffic)
  
  if (!dashboardData.value.traffic || !trafficChart.value) {
    console.warn('DOM')
    return
  }

  const data = dashboardData.value.traffic
  const metric = trafficMetric.value
  
  console.log(':', data)
  console.log(':', metric)
  
  // 
  if (!data.dates || !data[metric]) {
    console.warn(':', data)
    return
  }
  
  if (trafficChartInstance) {
    trafficChartInstance.destroy()
  }

  try {
    const ctx = trafficChart.value.getContext('2d')
    console.log('Canvas context:', ctx)
    
    trafficChartInstance = new Chart(ctx, {
      type: 'line',
      data: {
        labels: data.dates,
        datasets: [{
          label: getMetricLabel(metric),
          data: data[metric],
          borderColor: '#409EFF',
          backgroundColor: 'rgba(64, 158, 255, 0.1)',
          tension: 0.4,
          fill: true
        }]
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        plugins: {
          legend: {
            display: false
          }
        },
        scales: {
          y: {
            beginAtZero: true,
            grid: {
              color: 'rgba(0, 0, 0, 0.05)'
            }
          },
          x: {
            grid: {
              display: false
            }
          }
        }
      }
    })
    console.log('')
  } catch (error) {
    console.error(':', error)
  }
}

// 
const updateEngagementChart = () => {
  console.log('...')
  console.log('engagementChart ref:', engagementChart.value)
  console.log('userEngagement data:', dashboardData.value.userEngagement)
  
  if (!dashboardData.value.userEngagement || !engagementChart.value) {
    console.warn('DOM')
    return
  }

  const data = dashboardData.value.userEngagement
  
  // 
  if (!data.labels || !data.data || !data.colors) {
    console.warn(':', data)
    return
  }
  
  if (engagementChartInstance) {
    engagementChartInstance.destroy()
  }

  try {
    const ctx = engagementChart.value.getContext('2d')
    console.log('Canvas context:', ctx)
    
    engagementChartInstance = new Chart(ctx, {
      type: 'doughnut',
      data: {
        labels: data.labels,
        datasets: [{
          data: data.data,
          backgroundColor: data.colors,
          borderWidth: 0
        }]
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        plugins: {
          legend: {
            position: 'bottom'
          }
        }
      }
    })
    console.log('')
  } catch (error) {
    console.error(':', error)
  }
}

// 
const updateContentChart = async () => {
  console.log('...')
  console.log('contentChart ref:', contentChart.value)
  
  if (!contentChart.value) {
    console.warn('DOM')
    return
  }
  
  try {
    const response = await getContentAnalysis()
    const data = response
    
    console.log('API:', data)
    
    // 
    if (data && typeof data === 'object' && !data.includes && data.categories && data.performance) {
      if (contentChartInstance) {
        contentChartInstance.destroy()
      }

      const ctx = contentChart.value.getContext('2d')
      console.log('Canvas context:', ctx)
      
      contentChartInstance = new Chart(ctx, {
        type: 'bar',
        data: {
          labels: data.categories,
          datasets: [{
            label: '',
            data: data.performance,
            backgroundColor: 'rgba(64, 158, 255, 0.8)',
            borderRadius: 4
          }]
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          plugins: {
            legend: {
              display: false
            }
          },
          scales: {
            y: {
              beginAtZero: true,
              max: 100,
              grid: {
                color: 'rgba(0, 0, 0, 0.05)'
              }
            },
            x: {
              grid: {
                display: false
              }
            }
          }
        }
      })
      console.log('')
    } else {
      console.warn('')
      // 
      const defaultData = {
        categories: ['', '', '', '', ''],
        performance: [85, 72, 68, 91, 76]
      }
      
      if (contentChartInstance) {
        contentChartInstance.destroy()
      }
      
      const ctx = contentChart.value.getContext('2d')
      contentChartInstance = new Chart(ctx, {
        type: 'bar',
        data: {
          labels: defaultData.categories,
          datasets: [{
            label: '',
            data: defaultData.performance,
            backgroundColor: 'rgba(64, 158, 255, 0.8)',
            borderRadius: 4
          }]
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          plugins: {
            legend: {
              display: false
            }
          },
          scales: {
            y: {
              beginAtZero: true,
              max: 100,
              grid: {
                color: 'rgba(0, 0, 0, 0.05)'
              }
            },
            x: {
              grid: {
                display: false
              }
            }
          }
        }
      })
      console.log('')
    }
  } catch (error) {
    console.error(':', error)
    // 
    if (contentChart.value) {
      const defaultData = {
        categories: ['', '', '', '', ''],
        performance: [85, 72, 68, 91, 76]
      }
      
      if (contentChartInstance) {
        contentChartInstance.destroy()
      }
      
      const ctx = contentChart.value.getContext('2d')
      contentChartInstance = new Chart(ctx, {
        type: 'bar',
        data: {
          labels: defaultData.categories,
          datasets: [{
            label: '',
            data: defaultData.performance,
            backgroundColor: 'rgba(64, 158, 255, 0.8)',
            borderRadius: 4
          }]
        },
        options: {
          responsive: true,
          maintainAspectRatio: false,
          plugins: {
            legend: {
              display: false
            }
          },
          scales: {
            y: {
              beginAtZero: true,
              max: 100,
              grid: {
                color: 'rgba(0, 0, 0, 0.05)'
              }
            },
            x: {
              grid: {
                display: false
              }
            }
          }
        }
      })
      console.log('')
    }
  }
}

// 
const getMetricLabel = (metric) => {
  const labels = {
    reads: '',
    likes: '',
    shares: '',
    comments: ''
  }
  return labels[metric] || metric
}

// 
const updateCharts = async () => {
  console.log('...')
  console.log('dashboardData:', dashboardData.value)
  
  try {
    await updateTrafficChart()
    await updateEngagementChart()
    await updateContentChart()
    console.log('')
  } catch (error) {
    console.error(':', error)
  }
}

// 
const generateHotContent = () => {
  hotContent.value = [
    { rank: 1, title: '', category: '', reads: 12580, likes: 845, shares: 326, engagement: 8.5 },
    { rank: 2, title: '2024', category: '', reads: 10890, likes: 720, shares: 285, engagement: 7.8 },
    { rank: 3, title: '', category: '', reads: 9650, likes: 680, shares: 245, engagement: 7.2 },
    { rank: 4, title: '', category: '', reads: 8920, likes: 620, shares: 210, engagement: 6.9 },
    { rank: 5, title: '', category: '', reads: 7840, likes: 580, shares: 195, engagement: 6.5 }
  ]
}

onMounted(async () => {
  console.log('Dashboard...')
  
  // 
  dashboardData.value = {
    statistics: {
      totalReads: 12580,
      readsTrend: 12.5,
      avgLikesRate: 3.8,
      likesTrend: 2.3,
      shares: 845,
      sharesTrend: 5.7,
      comments: 326,
      commentsTrend: -1.2,
      totalContent: 156,
      contentGrowth: 8.5
    },
    traffic: {
      dates: ['12/01', '12/02', '12/03', '12/04', '12/05', '12/06', '12/07'],
      reads: [3200, 4500, 4880, 5200, 4800, 5600, 6100],
      likes: [120, 180, 165, 200, 185, 220, 240],
      shares: [230, 310, 305, 350, 320, 380, 420],
      comments: [45, 68, 52, 75, 60, 85, 95]
    },
    userEngagement: {
      labels: ['', '', '', ''],
      data: [470, 326, 845, 520],
      colors: ['#409EFF', '#67C23A', '#E6A23C', '#F56C6C']
    }
  }
  
  console.log(':', dashboardData.value)
  
  updateStatistics()
  generateHotContent()
  
  // DOM
  await nextTick()
  console.log('DOM...')
  
  // canvas
  setTimeout(async () => {
    console.log('...')
    await updateCharts()
  }, 100)
  
  // 
  await loadDashboardData()
})
</script>

<style scoped>
.dashboard-container {
  min-height: 100vh;
  background-color: #f5f7fa;
  padding: 20px;
}

.dashboard-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
  background: #ffffff;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
}

.header-left {
  display: flex;
  align-items: center;
  gap: 20px;
}

.back-button {
  border-radius: 8px;
}

.dashboard-title {
  margin: 0;
  color: #2c3e50;
  font-size: 24px;
  font-weight: 600;
}

.header-right {
  display: flex;
  gap: 15px;
  align-items: center;
}

.stats-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin-bottom: 30px;
}

.stat-card {
  background: #ffffff;
  border-radius: 12px;
  padding: 24px;
  display: flex;
  align-items: center;
  gap: 20px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.stat-card:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.15);
}

.stat-icon {
  width: 60px;
  height: 60px;
  border-radius: 12px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  font-size: 24px;
}

.stat-content {
  flex: 1;
}

.stat-value {
  font-size: 28px;
  font-weight: 700;
  color: #2c3e50;
  margin-bottom: 5px;
}

.stat-label {
  font-size: 14px;
  color: #7f8c8d;
  margin-bottom: 8px;
}

.stat-trend {
  font-size: 12px;
  font-weight: 600;
  display: flex;
  align-items: center;
  gap: 4px;
}

.stat-trend.positive {
  color: #27ae60;
}

.stat-trend.negative {
  color: #e74c3c;
}

.charts-section {
  display: grid;
  grid-template-columns: 2fr 1fr;
  gap: 20px;
  margin-bottom: 30px;
}

.analysis-section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
}

.chart-container {
  background: #ffffff;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
}

.chart-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
}

.chart-header h3 {
  margin: 0;
  color: #2c3e50;
  font-size: 18px;
  font-weight: 600;
}

.chart-content {
  position: relative;
  height: 300px;
}

.table-container {
  background: #ffffff;
  border-radius: 12px;
  padding: 24px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
}

.rank-badge {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 600;
  color: white;
  font-size: 14px;
}

.rank-1 { background: linear-gradient(45deg, #ffd700, #ffed4e); }
.rank-2 { background: linear-gradient(45deg, #c0c0c0, #e8e8e8); }
.rank-3 { background: linear-gradient(45deg, #cd7f32, #daa520); }
.rank-4, .rank-5 { background: #95a5a6; }

.engagement-rate {
  color: #27ae60;
  font-weight: 600;
}

.loading-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 9999;
}

.loading-overlay p {
  margin-top: 20px;
  color: #7f8c8d;
  font-size: 16px;
}

.no-data {
  grid-column: 1 / -1;
  text-align: center;
  padding: 40px;
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 2px 12px rgba(0, 0, 0, 0.1);
}

.no-data p {
  color: #7f8c8d;
  font-size: 16px;
  margin: 0;
}

/*  */
@media (max-width: 1200px) {
  .charts-section {
    grid-template-columns: 1fr;
  }
  
  .analysis-section {
    grid-template-columns: 1fr;
  }
}

@media (max-width: 768px) {
  .dashboard-header {
    flex-direction: column;
    gap: 15px;
    text-align: center;
  }
  
  .stats-grid {
    grid-template-columns: 1fr;
  }
  
  .chart-header {
    flex-direction: column;
    gap: 10px;
    align-items: flex-start;
  }
}
</style>